

cc.Class({
    extends: cc.Component,

    properties: {
        infomationBox: {
            default: null,
            type: cc.node
        },
        imageBox: {
            default: null,
            type: cc.node
        },
        selectBox: {
            default: null,
            type: cc.node
        },
    },

    init : function( infomation ){
        this.infomation = infomation
    },

    
    acceptCheck : function(  ) {

        var msg = this.home.infomation.dataInfo
        msg.budget += 1;
        msg.resource += 2;
        msg.health += 3;
        msg.popularity += 4;

        this.home.updateInfo(msg);
    }


});
